import UtilsAdapter from "./UtilsAdapter";

export default class Utils {
    public static scrollToTop(): void {
        window.scroll(0, 0);
    }

    public static setTitle(title: string): void {
        document.title = title;

    } public static gtagPageview(path: string): void {
        UtilsAdapter.gtagPageview(path);
    }
}