import React from 'react';
import { PropName, ArticleDataType, HTTP_YOUTUBE_URL, HTTPS_YOUTUBE_URL } from './Constant';

interface PropType {
  [PropName.ARTICLE_DATA]: ArticleDataType
}

export default class Article extends React.Component<PropType> {
  render(): JSX.Element {
    let embedcode: string = this.props[PropName.ARTICLE_DATA][PropName.EMBEDCODE];
    if (embedcode === '') {
      return <></>;
    }

    // 埋め込みコード内のURLがhttpになっているのでエラー防止のためにhttpsに置換する
    embedcode = embedcode.replace(HTTP_YOUTUBE_URL, HTTPS_YOUTUBE_URL);

    return (
      <div className="card m-2">
        <div className="card-body">
          <div dangerouslySetInnerHTML={{
            __html: embedcode
          }}>
          </div>
        </div>
      </div>
    );
  }
}