import React from 'react';
import { Link } from "react-router-dom";
import { PropName } from './Constant';

const STYLE: React.CSSProperties = {
  width: 'inherit'
};

interface PropType {
  [PropName.PAGE_NO]: string
}

export default class PageNavigator extends React.Component<PropType> {

  render(): JSX.Element {
    return (
      <div className="p-2 d-flex justify-content-around" style={STYLE}>
        <div>
          <Link to={this.getPreviousPage()} >
            <img src="/navigationj_prev.png" alt="prev" />
          </Link>
        </div>
        <div>
          <Link to={this.getNextPage()}>
            <img src="/navigationj_next.png" alt="next" />
          </Link>
        </div>
      </div>
    );
  }

  getNextPage(): string {
    const page: string = this.props[PropName.PAGE_NO];
    let pageNum: number = parseInt(page);
    pageNum++;
    return pageNum.toString();
  }

  getPreviousPage(): string {
    const page: string = this.props[PropName.PAGE_NO];
    let pageNum: number = parseInt(page);
    pageNum--;
    pageNum = Math.max(pageNum, 1);//ページの最小値
    return pageNum.toString();
  }
}