import React from 'react';
import Article from './Article';
import { PropName, ArticleDataType, ARTICLES_PER_PAGE, ARTICLE_DATA_URL } from './Constant';

interface PropType {
  [PropName.PAGE_NO]: string
}

interface StateType {
  [PropName.ARTICLE_DATA_LIST]: ArticleDataType[];
}

export default class MainSection extends React.Component<PropType, StateType> {
  constructor(props: PropType) {
    super(props);

    this.state = {
      [PropName.ARTICLE_DATA_LIST]: []
    };
  }

  render(): JSX.Element {
    return (
      <>
        {this.getActiveDataList().map((articleData, index) =>
          <Article articleData={articleData} key={index} />
        )}
      </>
    );
  }

  getActiveDataList(): ArticleDataType[] {
    const page: string = this.props[PropName.PAGE_NO];
    const startIndex: number = (parseInt(page) - 1) * ARTICLES_PER_PAGE;
    const endIndex: number = startIndex + ARTICLES_PER_PAGE;
    return this.state[PropName.ARTICLE_DATA_LIST].slice(startIndex, endIndex);
  }

  componentDidMount(): void {
    this.updateArticleDataList();
  }

  updateArticleDataList(): void {
    fetch(ARTICLE_DATA_URL)
      .then(response => response.json())
      .then(json => {
        const articleDataList: ArticleDataType[] = [];
        for (const item of json.feed.entry) {
          const articleData: ArticleDataType = {
            [PropName.EMBEDCODE]: item[`gsx$${[PropName.EMBEDCODE]}`].$t
          };
          articleDataList.push(articleData);
        }
        articleDataList.reverse();// 新しいもの順に並べ替える
        this.setState({
          [PropName.ARTICLE_DATA_LIST]: articleDataList
        });
      });
  }
}