import React from 'react';
import { RouteComponentProps } from 'react-router';
import Header from "./Header";
import MainSection from './MainSection';
import PageNavigator from "./PageNavigator";
import { PropName, BODY_STYLE } from './Constant';
import Utils from './Utils';

interface RouterParamPropType {
  [PropName.PAGE_NO]: string
}

interface PropType extends RouteComponentProps<RouterParamPropType> {
}

export default class App extends React.Component<PropType> {

  render(): JSX.Element {
    const pageNo: string = this.props.match.params[PropName.PAGE_NO];
    Utils.scrollToTop();
    Utils.setTitle(`zoo zoo town - page ${pageNo}`);
    Utils.gtagPageview(this.props.location.pathname);

    return (
      <div className="mx-auto d-flex flex-column align-items-center" style={BODY_STYLE}>
        <Header />

        <MainSection pageNo={pageNo} />

        <PageNavigator pageNo={pageNo} />
      </div>
    );
  }
}